
html, body{
  margin: 0;
  padding: 0;
  background-color:#131417;
  height: 100%;
  /* overflow: hidden; */
}

.App {
  /* text-align: center; */
  /* border: solid; */
  /* padding: 30px; */
}

*{
  font-family: Arial, Helvetica, sans-serif;
  color: white;
}

#options{
  border: solid;
  width: 30%;
  margin: 20px auto;
  border-radius:7px ;
}

#main{
  padding:20px;
}

.type-option{
  display: inline-block;
  /* border-left:solid white; */
  padding: 10px;
  cursor: pointer;
  user-select: none;
  width: 50%;
  box-sizing: border-box;
  text-align: center;
  transition: all 0.2s;
  height: 100%;
  vertical-align: top;
}

.type-option:last-child{
  /* border-right: none; */
}

.selected-option{
  color: black;
  background-color:white;
}

#motion{
  text-align: center;
  width: 50%;
  margin:auto;
}

canvas{
  border: 5px solid black;
  /* border:none; */
  margin:auto;
  background: linear-gradient(0deg, rgb(221, 221, 255), rgb(162, 255, 162) );
  width: 100%;
  box-sizing: border-box;
}

input[type="text"]{
  outline: none;
  border: none;
  box-sizing: border-box;
  font-size: 20px;
  padding:5px;
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
  color:black;
}

#t3{
  /* margin-bottom:50px; */
}

#t3 .menu{
  display: inline-block;
  /* border:10px solid black; */
  box-sizing: border-box;
  vertical-align:top;
  background-color: rgb(49, 49, 49);
}

#t3 .menu:nth-child(1){
  padding: 10px;
  width: 30%;
  display: none;
}
#t3 .menu:nth-child(2){
  /* width: 40%; */
  width: 50%;
}
#t3 .menu:nth-child(3){
  padding: 10px;
  width: 50%;
}

button{
  color: black;
}

#description{
  /* background-color:#222222; */
  /* background-color:#222222; */
  padding: 15px;
}

a{
  text-decoration: none;
}

h1 a{
  font-family: cursive, arial, helvetica;
  font-weight: 900;
  letter-spacing: 2px;
  /* margin-bottom:-10px; */
}

#app-view{
  /* background-color:red; */
  display: inline-block;
  margin-right: 15px;
  /* border: 10px solid black; */
  /* width: 10%; */
  width: 125px;
  background-color:#1e1f26;
  padding: 0px;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  z-index: 999 !important;
}

#app-view #app-view-name{
  margin-top: -10px;
  padding: 10px;
  font-size: 15px;
  font-weight: bold;
  z-index: 99;
  background-color:#1e1f26 ;
  position:  relative;
  /* position: absolute; */
  display: block;
}

#app-view img{
  /* height: 50%; */
  width: 100%;
  margin-bottom: 0px;
  border-radius: 10px 10px 0 0;
  /* aspect-ratio: 1/2; */
  transition: all 0.55s;
  z-index: -10;
}

#app-view:hover img{

    transform: scale(1.5);
  /* transform:matrix(1, 2, -1, 1, 80, 80); */
}

#form-area{
  /* width: 2000px !important; */
  /* background-color: red; */
  /* height: initial !important; */
  display: block !important;
  /* position: relative; */
  /* border: solid red; */
}

@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #app-view{
    width: 100px;

  }

  #menu-1{
    display: none !important;
  }

  #menu-2{
    width: 100% !important;
    
  }

  #motion{
    width: 100% !important;
  }

  #options{
    width: 100% !important;
  }
}